.section {
    padding-top: $padding-large;
    padding-bottom: $padding-large;

    &--extra-gap {
        padding-top: $padding-extra-large;
        padding-bottom: $padding-extra-large;
    }

    &--no-gap {
        padding-top: 0;
        padding-bottom: 0;
    }

    &--striped {
        background-color: $accent-color-ligth;
    }

    &--course-background {
        @media (min-width: $breakpoint-tablet) {
            position: relative;
            background-image: url('../images/course-header.jpg');
            background-repeat: no-repeat;
            background-size: cover;
            background-position: center 40%;

            &::before {
                content: '';
                position: absolute;
                background-color: rgba(0, 0, 0, 0.2);
                top: 0;
                bottom: 0;
                left: 0;
                right: 0;
            }
        }
    }

    &__wrapper {
        position: relative;
        &--container {
            max-width: 1240px;
            padding-left: $padding-small;
            padding-right: $padding-small;
            margin: 0 auto;
            @media (min-width: $breakpoint-tablet) {
                padding-left: 0;
                padding-right: 0;
            }
        }
    }

    &__header {
        text-align: center;
        margin-bottom: 2rem;
    }

    &__heading {
        font-size: 36px;
        font-weight: bold;
        margin-top: 0;
        margin-bottom: 1rem;
    }

    &__subheading {
        color: $primary-color;
        font-size: 22px;
        font-weight: bold;
        margin-top: 0;
        margin-bottom: 0.5rem;
    }

    &__media {
        max-width: 380px;
        margin: auto
    }

    &__content {
        &--header {
            @include flexBoxLayout;
        }

        &--curses {
            @include sectionGrid;
        }

        &--horizontal-centered {
            @include flexBoxLayout($alignItems: center);
        }

        &--info {
            @include flexBoxLayout($alignItems: flex-start, $justifyContent: center);

            & > * {
                padding-top: $padding-large;
                padding-bottom: $padding-large;

                &:first-child {
                    padding-top: 0;
                }
                &:last-child {
                    padding-bottom: 0;
                }

                @media (min-width: $breakpoint-tablet) {
                    padding-top: 0;
                    padding-bottom: 0;
                }
            }
        }

        &--features {
            @include sectionGrid($min: 360px);
        }
    }
}
