.logo {
    &--menu-fixed {
        @include flexBox($alignItems: center);
        padding-left: $padding-small;

        .logo__image {
            max-width: 64px;
            transition: all 0.5s ease;
        }
    }

    &--hidden {
        .logo__image {
            width: 0;
        }
    }

    &__redirect {
        display: contents;
        text-decoration: none;
    }

    &__image {
        max-width: 132px;

        @media (min-width: $breakpoint-tablet) {
            max-width: 244px;
        }
    }
}

.button {
    color: $primary-ligth-color;
    font-size: 16px;
    font-weight: 600;
    text-transform: uppercase;
    background-color: $primary-color;
    border-width: 1px;
    padding: $padding-small;
    border: 1px solid $primary-color;

    &--outline {
        color: $primary-color;
        background-color: $primary-ligth-color;
        &-whatsapp {
            color: $action-wp-color;
            background-color: $action-wp-color;
        }
    }

    &--whatsapp {
        background-color: $action-wp-color;
        border-color: $action-wp-color;
    }
}

.float {
    position: fixed;
    width: 60px;
    height: 60px;
    bottom: 40px;
    right: 40px;
    background-color: #25d366;
    color: #fff;
    border-radius: 50px;
    text-align: center;
    font-size: 30px;
    box-shadow: 2px 2px 3px #999;
    z-index: 100;
}

.my-float {
    margin-top: 16px;
}

.handorgel {
    border: none !important;
}

.learning {
    &__detail-items {
        @include flexBoxLayout($justifyContent: space-between);
        padding-top: $padding-small;

        [class*='__icon'] {
            max-width: 24px;
        }

        [class*='__heading'] {
            font-size: 16px;
        }
    }
}
