.header {
    &__heading {
        font-size: 32px;
        font-weight: bold;
        text-align: center;
        margin-top: $padding-extra-small;
        margin-bottom: $padding-large;

        @media (min-width: $breakpoint-tablet) {
            text-align: initial;
            font-size: 48px;
        }

        &--course {
            font-size: 32px;
            max-width: 600px;
            
            @media (min-width: $breakpoint-tablet) {
                color: $primary-ligth-color;
                text-align: initial;
                font-size: 60px;
            }
        }
    }

    &__image {
        width: 100%;
        max-height: 200px;
        object-fit: cover;
    }

    &__embed {
        width: 48px;
        margin-left: 4px;
        margin-right: 4px;

        @media (min-width: $breakpoint-tablet) {
            width: 64px;
            margin-left: 6px;
            margin-right: 6px;
        }
    }

    &__form-wrapper {
        color: $primary-color;
        margin: auto;

        &--theme-ligth {
            @media (min-width: $breakpoint-tablet) {
                color: $primary-ligth-color;
            }
        }
    }
}
